<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Pretermination
      </div>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table :headers="headers" :items="data.data" loading-text="Loading... Please wait" class="elevation-1" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.Createdate`]="{ item }">
            {{ toDateTime(item.Createdate) }}
        </template>
        <template #[`item.view`]="{ item }">
          <v-btn :disabled="checkView(item.Status)" icon color="success" @click="detail(item)">
            <v-icon small>
              mdi-eye
            </v-icon>
          </v-btn>
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn :disabled="checkStatus(item.Status)" color="success" @click="edit(item)">
            Approval
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <detailBlog/>
    <editBlog/>
    <createBlog/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import detailBlog from '@/components/Blog/Detail.vue'
import editBlog from '@/components/Blog/Update.vue'
import createBlog from '@/components/Blog/Create.vue'
import Utils from '@/utils/util'
import store from '@/store'
export default {
  name: 'PreterminationPage',
  data: () => ({
    search: '',
    headers: [
      {
        text: '#',
        align: 'start',
        sortable: false,
        value: 'index'
      },
      { text: 'NIK', sortable: false, value: 'NIK' },
      { text: 'Nama', sortable: false, value: 'Requestby' },
      { text: 'Tgl Pengajuan', sortable: false, value: 'TglPengajuan' },
      { text: 'Sisa OS', sortable: false, value: 'SisaOS' },
      { text: 'Status', sortable: false, value: 'Status' },
      { text: 'View', sortable: false, value: 'view' },
      { text: 'Action', sortable: false, value: 'action' }
    ]
  }),
  components: {
    detailBlog, editBlog, createBlog
  },
  mounted: function () {
    store.dispatch('pretermination/reqData')
  },
  computed: {
    ...mapState('pretermination', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('pretermination', [
      'edit', 'detail', 'hapus', 'create'
    ]),
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    },
    checkStatus (status) {
      if (status === 'PT2' || status === 'PT3' || status === 'PAI' || status === 'RJT') {
        return 'disabled'
      }
    },
    checkView (status) {
      if (status !== 'PT3') {
        return 'disabled'
      }
    }
  }
}
</script>
